#videopage {
    display: flex;
    min-width: calc(480px * 16/9 + 3 * 24px + 300px);
    max-width: calc(1280px + 402px + 3 * 24px);
    margin: 0 auto;
    justify-content: center;
}

#rec {
    width: 402px;
    min-width: 300px;
    padding: 24px 24px 0 0;
}

.v-videos {
    display: flex;
    flex: 1;
    flex-direction: column;
    max-width: calc((100vh - 216px) * 16/9);
    height: auto;
    padding: 24px 24px 0 0;
    margin: 0 0 0 24px;
    text-decoration: none;
}

.v-v video {
    width: 100%;
    height: 100%;
}

#rv-title {
    font-size: 1.2rem;
    font-weight: 600;
    color: #f1f1f1;
    word-wrap: break-word;
}

.v-details {
    display: flex;
    flex-direction: column;
    margin: 12px 0 12px;
}

#des {
    /* margin: 6px 0 0; */
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    padding: 10px;
    word-break: break-all;
}

.v-r-videos {
    height: auto;
    margin: 0 0 20px 0;
    cursor: pointer;
    text-decoration: none;
    gap:10px
}

.v-r-th {
    /* height: calc((50%*9)/16); */
    width: 50%;
    overflow: hidden;
    border-radius: 10px;
}
#vc{display: none;}
.v-r-th img {
    height: 100%;
    width: 100%;
    filter: brightness(1.5);
    object-fit: cover;
}
.v-det{overflow: hidden;}

.v-r-data{width: 50%;}
.subbtn{
    height: 30px;
    width: 95px;
    border-radius: 40px;
    border: none;
    cursor: pointer;
}

@media only screen and (max-width: 600px) {
    #videopage {
        flex-direction: column;
        min-width: 0;
    }
    #des{margin: 0 10px;}
    .v-videos {
        padding: 24px 0 0;
        margin: 0;
    }
    #vc{display: block;}
    .v-details {
        margin: 12px 10px 24px;
    }
    .v-r-videos{flex-direction: column;gap:0;}
    .v-r-data{width: 100%;padding: 10px 10px 0;}
    .v-r-th{width: 100%;border-radius: 0;}
    #rec {
        width: 100%;
        padding: 32px 0 0;
    }
    .v-det{padding-left: 10px;}
}