.login-m{
    width: 100%;
}
#loginform{
    max-width: 300px;
    display: flex;
    flex-direction: column;
    gap: 12px;
    /* align-items: center; */
    margin: 50px auto;
}
#regisform{
    max-width: 80vw;
    margin: 0 auto 100px;
}
#vidform{
    max-width: 80vw;
    margin: 20px auto 200px;
}
.a-v-i{
    display: flex;
    flex-direction: column;
    gap: 12px;
    width: 60vw;
    margin: 0 auto;
    padding: 50px 0 0;
}
.rf-t{
    gap: 10vw;
    padding-bottom: 50px;
}
label{
    font-size: 18px;
}
.f-input{
    background-color: hsl(0, 0%, 7%);
    height: 40px;
    border-bottom: 1px solid rgba(255,255,255,0.1);
    border-top: 1px solid hsl(0, 0%, 7%);
    border-left: 1px solid hsl(0, 0%, 7%);
    border-right: none;
    color: white;
    padding-left: 8px;
    font-size: 14px;
    font-family: inherit;
    /* border-radius: 40px; */
}
::placeholder{
    opacity: .5;
}
.signinbtn{
    padding: 8px 22px;
    width: fit-content;
    margin: 15px auto 5px;
    background-color: #3ea6ff;
    font-size: 16px;
    font-weight: 600;
    border-radius: 40px;
    border: none;
}
.signinbtn:hover{
    background-color: #65b5fc;
}
.flex-c{
    display: flex;
    flex-direction: column;
    gap: 12px;
    flex: 1;
}
.advidtitle{
    display: flex;
    align-items: center;
}

@media only screen and (max-width: 600px){
    .rf-t{flex-direction: column;}
    .advidtitle{font-size: .6rem;}
    .a-v-i{width: 100%;}
}