.nav-link{
    text-decoration: none;
    color: #aaa;
    letter-spacing: .5px;
    text-transform: uppercase;
    padding: 0 32px;
    font-size: .8rem;
    font-weight: 600;
    height: 100%;
    align-items: center;
  }
.active{
    font-weight: 600;
    color: white;
    border-bottom: 3px solid #aaa;
}
#myvideo{
    display: flex;
    margin: auto;
    max-width: 60vw;
    padding-top: 24px;
    flex-wrap: wrap;
}
.p-l{
    padding-left: 20px;
}
.lighttext{
    opacity: .5;
    font-size: .9rem;
    line-height: 1.6;
}
.banner{
    width: 100%;
    height: 325px;
    overflow:hidden;
}
.banner img{
    /* test */
    transform: scale(1.2);
    width: 100%;
    object-fit: cover;
}
.f-a{
    flex:2;
    padding-right: 96px;
    min-width: 0;
    word-wrap: break-word;
}
#logoutbtn{
    color: black;
    background-color: #3ea6ff;
    height: 35px;
    border: none;
    font-size: 16px;
    width: 120px;
    border-radius: 40px;
}
#logoutbtn:hover{
    background-color: #65b5fc;
}
.ab-ch{
    padding:16px 140px 4px;
    width: 100%;
    justify-content: space-between;
}
.my-nav{
    height: 48px;
    padding:0 140px;
    width: calc(100% - 250px);
    border-bottom: 1px solid rgba(255,255,255,0.1);
}
.outlet{padding: 0 140px;}

@media only screen and (max-width: 1400px){
    .banner{height:240px}
}
@media only screen and (max-width: 600px) {
    .banner{height:150px}
    .ab-ch{padding:16px 20px 4px;}
    .my-nav{padding: 0 20px;width: 100%;}
    .outlet{padding: 0;}
    #myvideo{max-width: 100%;justify-content: center;}
    .f-a{padding: 0 0 0 20px;}
}