.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 56px;
  width: 100%;
  padding: 0 22px;
  position: fixed;
  top: 0;
  z-index: 1;
  background-color: #0f0f0f;
}
.addvid{
  display: flex;
  align-items: center;
}
#signin{
    color: #f1f1f1;
    padding: 8px 16px;
    border: 1px solid rgba(255,255,255,0.2);
    border-radius: 40px;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: .05rem;
    color: #3ea6ff;
}
a{text-decoration: none;}
#v-title{
  font-size: 1rem;
  font-weight: 500;
  margin: 0 0 4px 0;
  line-height: 1.4rem;
  /* overflow: hidden; */
  color: #f1f1f1;
}
.v-c-name{
  font-size: .9rem;
  font-weight: 400;
  color: rgb(124, 124, 124);
  line-height: 1.2rem;
}
.left {
  display: flex;
  align-items: center;
  gap: 24px;
}

.left #menu {
  padding: 0 7px;
  cursor: pointer;
}

.search {
  display: flex;
}

.search form {
  width: 40vw;
  max-width: 572px;
  display: flex;
  height: 40px;
  background-color: #0f0f0f;
}

.search input {
  width: 100%;
  padding: 10px;
  border: 0;
  height: 100%;
  border: 1px solid hsl(0, 0%, 18.82%);
  border-radius: 40px 0 0 40px;
  background-color: hsl(0, 0%, 7%);
  color:hsla(0, 100%, 100%, 0.88);
}

input:focus {
  outline: none;
  border: 1px solid #1c62b9;
}

.search form button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 60px;
  border-radius: 0 40px 40px 0;
  border-top: 1px solid hsl(0, 0%, 18.82%);
  border-right: 1px solid hsl(0, 0%, 18.82%);
  border-bottom: 1px solid hsl(0, 0%, 18.82%);
  border-left: 0;
  background-color: hsla(0, 0%, 100%, 0.08);
}

.material-icons {
  color: rgb(100, 100, 100);
  padding: 0 7px;
  cursor: pointer;
}
.profile{
  border-radius: 50%;
  overflow: hidden;
  object-fit: cover;
}
main{
  width: 100%;
  height: auto;
}
.flex{display: flex;}
.m-s{
  flex-wrap: wrap;
  padding: 32px 20px 0 76.16px;
}
.m-videos, .m-a-videos{
  display: flex;
  flex-direction: column;
  width: 27.35vw;
  height: auto;
  margin: 0 8px 40px 8px;
  cursor: pointer;
  text-decoration: none;
}
.m-a-videos{width: 300px !important;}
.m-a-th{height: 168.75px !important;}
.m-th, .m-a-th {
  height: calc((27.35vw*9)/16);
  width: 100%;
  overflow: hidden;
  border-radius: 15px;
}
.m-th img, .m-a-th img{
  height: 100%;
  width: 100%;
  filter: brightness(1.5);
  object-fit: cover;
}
.m-c-img{
  border-radius: 40px;
  object-fit: cover;
}
.side-bar{
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: hsl(0, 0%, 7%);
  width: 190px;
  padding: 10px 0 0 10px;
}
.m-sidebar{
  position: fixed;
  z-index: 1;
  display: flex;
  height: 100%;
  width: 100%;
}
.dot {
  align-content: center;
  height: 4px;
  width: 4px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  background-color: rgb(124, 124, 124);
}
#t-bar{display: block;}
.m-v-data{padding: 10px 0 0;}
.mob-fix-foot{
  display: none;
  height: 56px;
  width: 100%;
  position: fixed;
  bottom: 0;
  background-color: #0f0f0f;
  gap: 40vw;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 600px) {
  .m-s{flex-direction: column;padding: 20px 0 0 0;}
  #t-bar{display: none;}
  .header{padding: 0 10px;}
  .m-videos{margin: 0 0 40px 0;width: 100%;}
  .m-th{height: calc((100vw*9)/16);border-radius: 0;}
  .m-v-data{padding: 15px 15px 0;}
  .mob-fix-foot{display: flex;}
  .addvid{display: none;}
  .side-bar{width: 390px;}
}
